.account_alt {
	text-align: center;
}

.button {
	font-size: 20px;
	border-radius: 5px;
	height: 30px;
	outline: none !important; 
	padding: 0;
	text-align: center;
	background-color: silver;
	border: 1px solid black;
}

.button:hover {
	background-color: gray;
}

.button.add {
	font-weight: bold;
	font-size: 30px;
	float: left;
	height: 30px;
	line-height: 0px;
	margin-top: 3px;
}

.button.add.create_poll {
	margin-left: 50px;
}

.button.delete {
	font-size: 20px;
	background-color: silver;
	margin-right: 45px;
	float: right;
	border-radius: 5px;
	padding-bottom: 0px;
}

.button.delete.create_poll {
	margin-right: 10px;
}
.button.nav {
	font-size: 20px;
	height: 50px;
	width: 100px;
	line-height: 50px;
	text-align: center;
	border: none;
	background-color: transparent;
}

.button.nav:hover {
	color: 	#C0C0C0;
	cursor: pointer;
}

.button.nav.last {
	margin-right: 10px;
}

.button.poll {
	width: 250px;	
	font-size: 16px;
	min-height: 30px;
	height: 100%;
	text-align: center;
	background-color: #01FF70;
	margin-bottom: 5px;
}

.button.poll:hover {
	background-color: #3D9970
}

.button.poll.home {
	margin-left: 25px;
}

.button.small {
	width: 30px;
}

.button.submit {
	float: right;
	margin-right: 65px;
	margin-bottom: 20px;
	width: 70px;
}

.button.save_vote {
	display: inline-block;
	font-size: 16px;
	float: right;
	width: 100px;
}

.button.vote {
	font-size: 16px;
	margin-bottom: 3px;
	min-height: 30px;
	width: 200px;
	background-color: #7FDBFF;
	padding: none;
	display: block;
}

.button.vote:hover {
	background-color: #0074D9;
}

.container {
	width: 300px;
	padding: 0;
	margin-left: auto;
	margin-right: auto;
}

.container.vote {
	width: 200px;
}

.description {
	width: 300px;
	text-align: center;
	margin-left: auto;
	margin-right: auto;
	font-size: 16px;
	margin-bottom: 30px;
}

.fade {
  opacity: 0;
  -webkit-transition: opacity 0.15s linear;
  -o-transition: opacity 0.15s linear;
  transition: opacity 0.15s linear;
}

.fade.in {
  opacity: 1;
}

h1 {
	text-align: center;
	margin-bottom: 30px;
	color: #3D9970;
}

h3,h4 {
	text-align: center;
}

input {
	resize: none;
	height: 40px;
	width: 200px !important;
	display: block;
	border-radius: 5px;
	border: 1px black solid;
	margin-bottom: 3px;
	margin-left: 50px;
}

.input {
	height: 40px;
	width: 200px !important;
	margin-top: 3px;
	margin-left: 50px;
	display: block;	
}

.input.add_vote {
	margin-left: 0px
}

.link {
	cursor: pointer;
}

.link:hover {
	text-decoration: none
}

.loading {
	text-align: center;
}

.main {
	margin-top: 60px;
}

.message {
	margin-top: 10px;
	text-align: center;
	width: 250px;
	margin-right: auto;
	margin-left: auto	
}

.no_link_effect {
	text-decoration: none !important;
	color: black;
}

.no_link_effect:hover {
	color: #C0C0C0;	
}

.pie {
	margin-right: auto;
	margin-left: auto;
	max-width: 400px;
}

.save {
	display: block;
	font-size: 16px;
	width: 100px;
	margin-left: 150px;
	margin-top: 3px;
}

textarea {
	resize: none;
	height: 40px;
	display: inline-block;
	border-radius: 5px;
}


